import axios from "axios";
import { CurrenciesObject } from "@/types/CurrenciesObject";
import { Objects } from "@/types/Objects";

const getConvertibleCurrencies = async (
  objects: Objects,
  currencies: string[]
) => {
  const currenciesObject: CurrenciesObject = {};
  try {
    for (const currency of currencies) {
      if (currenciesObject[currency] === undefined) {
        const response = await axios(
          `https://cdn.jsdelivr.net/gh/fawazahmed0/currency-api@1/latest/currencies/${currency.toLowerCase()}/rub.json`
        );
        currenciesObject[currency] = response.data.rub;
      }
    }
    return currenciesObject;
  } catch (error) {
    console.error(error);
  }
};

export default getConvertibleCurrencies;
