
import { PropType, defineComponent } from "vue";
import ThreeDotsIcon from "./Icons/ThreeDotsIcon.vue";
import { LanguageData } from "@/types/LanguageData";
import ModalWindowObject from "./ModalWindowObject.vue";
import { CurrenciesObject } from "@/types/CurrenciesObject";
import { Object } from "@/types/Object";

export default defineComponent({
  name: "MainTableObjectsMoreButton",
  props: {
    index: {
      type: Number,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    currenciesObject: {
      type: Object as PropType<CurrenciesObject>,
      required: true,
    },
  },
  components: { ThreeDotsIcon, ModalWindowObject },
});
