
import { SelectedPriceRange } from "@/types/DataOfFiltres";
import { LanguageData } from "@/types/LanguageData";
import { Object } from "@/types/Object";
import { PropType, defineComponent, ref } from "vue";

export default defineComponent({
  name: "FilterPrice",
  props: {
    currencies: {
      type: Array as PropType<Array<Object["currency"]>>,
      required: true,
    },
    selectedPriceRange: {
      type: Object as PropType<SelectedPriceRange>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
  emits: ["update:selectedPriceRange"],
  setup(props, { emit }) {
    const localFrom = ref(props.selectedPriceRange.from);
    const localTo = ref(props.selectedPriceRange.to);
    const localCurrency = ref(props.selectedPriceRange.currency);

    const updateSelectedPriceRange = () => {
      emit("update:selectedPriceRange", {
        from: localFrom.value,
        to: localTo.value,
        currency: localCurrency.value,
      });
    };

    return {
      localCurrency,
      localFrom,
      localTo,
      updateSelectedPriceRange,
    };
  },
});
