
import { Object } from "@/types/Object";
import { PropType, Ref, defineComponent, ref } from "vue";
import AccordionActions from "./AccordionActions.vue";
import { LanguageData } from "@/types/LanguageData";
import FieldTitleAndValue from "./FieldTitleAndValue.vue";
import formatterOfNumber from "@/utils/formatterOfNumber";
import getReadableDateOfCompletion from "@/utils/getReadableDateOfCompletion";
import { CurrenciesObject } from "@/types/CurrenciesObject";
import getGoogleDriveLink from "@/utils/getGoogleDriveLink";

export default defineComponent({
  name: "ModalWindow",
  components: { AccordionActions, FieldTitleAndValue },
  props: {
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    currenciesObject: {
      type: Object as PropType<CurrenciesObject>,
      required: true,
    },
  },
  setup() {
    const edit: Ref<Object> = ref({
      name: "",
      link: "",
      district: "",
      coordinates: [0, 0],
      layout: "",
      dateOfCompletion: "",
      currency: "",
      price: 0,
    });
    const setEditParameters = (object: Object) => {
      edit.value = {
        ...object,
        link: `https://drive.google.com/drive/folders/${object.link}`,
      };
    };
    return {
      formatterOfNumber,
      getReadableDateOfCompletion,
      edit,
      setEditParameters,
      getGoogleDriveLink,
    };
  },
});
