
import { LanguageData } from "@/types/LanguageData";
import { Object } from "@/types/Object";
import { PropType, Ref, computed, defineComponent, ref } from "vue";

export default defineComponent({
  name: "FilterDistricts",
  props: {
    districts: {
      type: Array as PropType<Array<Object["district"]>>,
      required: true,
    },
    selectedDistricts: {
      type: Array as PropType<Array<Object["district"]>>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
  emits: ["update:selectedDistricts"],
  setup(props, { emit }) {
    const localSelectedDistricts: Ref<Array<Object["district"]>> = ref([]);
    const selectedDistrictsLength = computed(
      () => props.selectedDistricts.length
    );
    const reset = () => {
      localSelectedDistricts.value = [];
      emit("update:selectedDistricts", localSelectedDistricts.value);
    };
    const updateCheckedDistricts = () => {
      emit("update:selectedDistricts", localSelectedDistricts.value);
    };
    return {
      localSelectedDistricts,
      selectedDistrictsLength,
      reset,
      updateCheckedDistricts,
    };
  },
});
