
import { LanguageData } from "@/types/LanguageData";
import { Objects } from "@/types/Objects";
import formatterOfNumber from "@/utils/formatterOfNumber";
import { PropType, defineComponent } from "vue";
import SortByElements from "./SortByElements.vue";
import { SortBy } from "@/types/SortBy";
import { CurrenciesObject } from "@/types/CurrenciesObject";
import MainTableObjectsMoreButton from "./MainTableObjectsMoreButton.vue";
import MainTableObjectsName from "./MainTableObjectsName.vue";

export default defineComponent({
  name: "MainTableObjects",
  emits: ["update:sortBy"],
  components: {
    SortByElements,
    MainTableObjectsMoreButton,
    MainTableObjectsName,
  },
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    sortBy: {
      type: String as PropType<SortBy>,
      required: true,
    },
    currenciesObject: {
      type: Object as PropType<CurrenciesObject>,
      required: true,
    },
  },
  setup() {
    return {
      formatterOfNumber,
    };
  },
});
