
import { SelectedDateOfCompletionRange } from "@/types/DataOfFiltres";
import { LanguageData } from "@/types/LanguageData";
import { PropType, defineComponent, ref } from "vue";

export default defineComponent({
  name: "FilterDateOfCompletion",
  props: {
    selectedDateOfCompletionRange: {
      type: Object as PropType<SelectedDateOfCompletionRange>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
  emits: ["update:selectedDateOfCompletionRange"],
  setup(props, { emit }) {
    const localDateFrom = ref(props.selectedDateOfCompletionRange.from);
    const localDateTo = ref(props.selectedDateOfCompletionRange.to);

    const updateSelectedDateOfCompletionRange = () => {
      emit("update:selectedDateOfCompletionRange", {
        from: localDateFrom.value,
        to: localDateTo.value,
      });
    };
    return {
      localDateFrom,
      localDateTo,
      updateSelectedDateOfCompletionRange,
    };
  },
});
