
import { Object } from "@/types/Object";
import { PropType, defineComponent } from "vue";
import AccordionActionChange from "./AccordionActionChange.vue";
import { LanguageData } from "@/types/LanguageData";
import AccordionActionDelete from "./AccordionActionDelete.vue";

export default defineComponent({
  name: "AccordionActions",
  emits: ["setEditParams", "updateEditParams"],
  props: {
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    edit: {
      type: Object as PropType<Object>,
      required: true,
    },
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  setup() {},
  components: { AccordionActionChange, AccordionActionDelete },
});
