import { SendToTelegramURLParams } from "@/types/SendToTelegramURLParams";
import { SendToTelegramType } from "@/types/SendToTelegramType";
import getDataFromUser from "@/utils/getDataFromUser";
import { Object } from "@/types/Object";
import axios from "axios";
import getGoogleDriveLink from "@/utils/getGoogleDriveLink";

type Params<T extends SendToTelegramType> = {
  type: T;
  obj_1: Object;
  obj_2?: T extends "edit" ? Object : never;
  reason?: T extends "delete" ? string : never;
};

const sendToTelegram = async <T extends SendToTelegramType>({
  type,
  obj_1,
  obj_2,
  reason,
}: Params<T>) => {
  try {
    const URL = `${window.location.origin}/#/changes`;
    let userData = getDataFromUser();
    if (userData === false) {
      throw new Error("User data is missing");
    }
    userData = userData as {
      name: string;
      token: string;
    };

    let params: SendToTelegramURLParams = {
      from: encodeURIComponent(userData.name),
      type: type,
      obj_1: encodeURIComponent(
        JSON.stringify({
          ...obj_1,
          link: getGoogleDriveLink(obj_1.link),
          coordinates: `[ ${obj_1.coordinates[0]},${obj_1.coordinates[1]} ]`,
        })
      ),
    };
    if (type === "edit") {
      params = {
        ...params,
        obj_2: encodeURIComponent(
          JSON.stringify({
            ...(obj_2 as Object),
            coordinates: `[ ${(obj_2 as Object).coordinates} ]`,
          })
        ),
      };
    }
    if (type === "delete") {
      params = {
        ...params,
        reason: encodeURIComponent(reason as string),
      };
    }

    const hrefURL = `${URL}?from=${params.from}&type=${params.type}&obj_1=${
      params.obj_1
    }${params.type === "edit" ? `&obj_2=${params.obj_2}` : ""}${
      params.type === "delete" ? `&reason=${params.reason}` : ""
    }`;
    const text = `<b>${
      userData.name
    }</b> прислал объект с целью <a href="${hrefURL}">${params.type.toUpperCase()}</a> ${
      obj_1.name
    }`;

    return await axios.post(
      `https://api.telegram.org/bot${
        userData.token
      }/sendMessage?chat_id=1049752057&text=${encodeURIComponent(
        text
      )}&parse_mode=HTML`
    );
  } catch (err) {
    console.error(err);
    throw err;
  }
};

export default sendToTelegram;
