
import { PropType, defineComponent } from "vue";
import SortByElement from "@/components/SortByElement.vue";
import { SortBy } from "@/types/SortBy";

export default defineComponent({
  name: "SortByElements",
  emits: ["update:sortBy"],
  props: {
    sortBy: {
      type: String as PropType<SortBy>,
      required: true,
    },
  },
  components: { SortByElement },
});
