import { GetDataFromUser } from "@/types/GetDataFromUser";

const getDataFromUser: GetDataFromUser = (shouldSet = false) => {
  let name = localStorage.name;
  let token = localStorage.token;
  if (!name || shouldSet) {
    name = prompt("Name") as string;
    if (!name) {
      return false;
    } else {
      localStorage.name = name as string;
    }
  }
  if (!token || shouldSet) {
    token = prompt("Token") as string;
    if (!token) {
      return false;
    } else {
      localStorage.token = token as string;
    }
  }

  return {
    name,
    token,
  };
};

export default getDataFromUser;
