import { Objects } from "@/types/Objects";

const getDataOfFiltres = (objects: Objects) => {
  const pricesFromObjects = objects.map((item) => item.price);
  const datesOfCompletionFromObjects = objects.map((item) =>
    Date.parse(item.dateOfCompletion)
  );
  const getDateToISOString = (number: number) => {
    const date = new Date(number);
    return date.toISOString().slice(0, 7);
  };

  const dataOfFiltres = {
    selectedDistricts: [],
    selectedLayouts: [],
    selectedPriceRange: {
      from: Math.min(...pricesFromObjects),
      to: Math.max(...pricesFromObjects),
      currency: "none",
    },
    selectedDateOfCompletionRange: {
      from: getDateToISOString(Math.min(...datesOfCompletionFromObjects)),
      to: getDateToISOString(Math.max(...datesOfCompletionFromObjects)),
    },
  };

  return dataOfFiltres;
};

export default getDataOfFiltres;
