
import { PropType, defineComponent } from "vue";
import ModalWindowStatistics from "./ModalWindowStatistics.vue";
import { Objects } from "@/types/Objects";
import { LanguageData } from "@/types/LanguageData";
import { CurrenciesObject } from "@/types/CurrenciesObject";
import StatisticsIcon from "./Icons/StatisticsIcon.vue";

export default defineComponent({
  name: "HeaderComponent",
  emits: ["update:language", "setUserData"],
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    currenciesObject: {
      type: Object as PropType<CurrenciesObject>,
      required: true,
    },
  },
  components: { ModalWindowStatistics, StatisticsIcon },
});
