
import { LanguageData } from "@/types/LanguageData";
import { Objects } from "@/types/Objects";
import copyResultsProcess from "@/utils/copyResultsProcess";
import { PropType, defineComponent, ref } from "vue";

export default defineComponent({
  name: "CopyObjectsButton",
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
  setup(props) {
    const wasCopied = ref(false);

    const copyResults = () => {
      wasCopied.value = true;
      const text = props.objects
        .map(
          (item, index) =>
            `${index + 1}. ${item.name}(${item.layout}) - ${item.district} - ${
              item.price
            } ${item.currency} - ${item.dateOfCompletion}`
        )
        .join("\n");
      copyResultsProcess(text);
      setTimeout(() => (wasCopied.value = false), 3500);
    };
    return {
      copyResults,
      wasCopied,
    };
  },
});
