
import { SortBy } from "@/types/SortBy.js";
import { PropType, defineComponent } from "vue";

export default defineComponent({
  name: "SortByElement",
  emits: ["update:sortBy"],
  props: {
    NAME_ASCENDING: {
      type: String as PropType<SortBy>,
      required: true,
    },
    NAME_DESCENTING: {
      type: String as PropType<SortBy>,
      required: true,
    },
    sortBy: {
      type: String as PropType<SortBy>,
      required: true,
    },
  },
  setup() {},
});
