
import { LanguageData } from "@/types/LanguageData";
import { PropType, defineComponent } from "vue";

export default defineComponent({
  name: "InputSearchByObjects",
  emits: ["update:value"],
  props: {
    value: {
      type: String,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
});
