const compareByNumber = (name: string, shouldUseDateParse = false) => {
  return (a: any, b: any) => {
    const aNumber: number = shouldUseDateParse
      ? Date.parse(a[name] as string)
      : (a[name] as number);
    const bNumber: number = shouldUseDateParse
      ? Date.parse(b[name] as string)
      : (b[name] as number);
    if (aNumber < bNumber) {
      return -1;
    }
    if (aNumber > bNumber) {
      return 1;
    }
    return 0;
  };
};

export default compareByNumber;
