
import { LanguageData } from "@/types/LanguageData";
import { Object } from "@/types/Object";
import { ref, computed, defineComponent, PropType } from "vue";

export default defineComponent({
  name: "FilterLayouts",
  props: {
    layouts: {
      type: Array as PropType<Array<Object["layout"]>>,
      required: true,
    },
    selectedLayouts: {
      type: Array as PropType<Array<Object["layout"]>>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
  emits: ["update:selectedLayouts"],
  setup(props, { emit }) {
    const localSelectedLayouts = ref([]);

    const selectedLayoutsLength = computed(() => props.selectedLayouts.length);
    const reset = () => {
      localSelectedLayouts.value = [];
      emit("update:selectedLayouts", localSelectedLayouts.value);
    };

    const updateSelectedLayouts = () => {
      emit("update:selectedLayouts", localSelectedLayouts.value);
    };
    return {
      localSelectedLayouts,
      selectedLayoutsLength,
      reset,
      updateSelectedLayouts,
    };
  },
});
