import { SortFilteredObjects } from "@/types/SortFilteredObjects";
import compareAlphabetically from "@/utils/compareAlphabetically";
import compareByNumber from "./compareByNumber";

const sortFilteredObjects: SortFilteredObjects = ({ objects, by }) => {
  switch (by) {
    case "":
      return objects;
    case "NAME_ASCENDING":
      objects.sort(compareAlphabetically("name"));
      return objects.reverse();
    case "NAME_DESCENTING":
      objects.sort(compareAlphabetically("name"));
      return objects;
    case "DISTRICT_ASCENDING":
      objects.sort(compareAlphabetically("district"));
      return objects;
    case "DISTRICT_DESCENTING":
      objects.sort(compareAlphabetically("district"));
      return objects.reverse();
    case "LAYOUT_ASCENDING":
      objects.sort(compareAlphabetically("layout"));
      return objects.reverse();
    case "LAYOUT_DESCENTING":
      objects.sort(compareAlphabetically("layout"));
      return objects;
    case "PRICE_ASCENDING":
      objects.sort(compareByNumber("price"));
      return objects.reverse();
    case "PRICE_DESCENTING":
      objects.sort(compareByNumber("price"));
      return objects;
    case "DATE_OF_COMPLETION_ASCENDING":
      objects.sort(compareByNumber("dateOfCompletion", true));
      return objects.reverse();
    case "DATE_OF_COMPLETION_DESCENTING":
      objects.sort(compareByNumber("dateOfCompletion", true));
      return objects;
    default:
      return objects;
  }
};

export default sortFilteredObjects;
