import { GetFilteredObjects } from "@/types/GetFilteredObjects";

const getFilteredObjects: GetFilteredObjects = ({ objects, dataOfFiltres }) => {
  const results = [];

  for (const item of objects) {
    let match = true;
    if (dataOfFiltres.selectedDistricts.length > 0) {
      if (!dataOfFiltres.selectedDistricts.includes(item.district)) {
        match = false;
      }
    }

    if (dataOfFiltres.selectedLayouts.length > 0) {
      if (!dataOfFiltres.selectedLayouts.includes(item.layout)) {
        match = false;
      }
    }

    if (dataOfFiltres.selectedPriceRange.currency !== "none") {
      if (item.currency !== dataOfFiltres.selectedPriceRange.currency) {
        match = false;
      }
    }

    if (
      item.price < dataOfFiltres.selectedPriceRange.from ||
      item.price > dataOfFiltres.selectedPriceRange.to
    ) {
      match = false;
    }

    const itemDate = Date.parse(item.dateOfCompletion);
    const fromDate = Date.parse(
      dataOfFiltres.selectedDateOfCompletionRange.from
    );
    const toDate = Date.parse(dataOfFiltres.selectedDateOfCompletionRange.to);

    if (itemDate < fromDate || itemDate > toDate) {
      match = false;
    }

    if (match) {
      results.push(item);
    }
  }
  return results;
};

export default getFilteredObjects;
