const colors = [
  "#FF0000",
  "#00FF00",
  "#0000FF",
  "#FFFF00",
  "#00FFFF",
  "#FF00FF",
  "#800000",
  "#008000",
  "#000080",
  "#808000",
  "#800080",
  "#008080",
  "#C0C0C0",
  "#808080",
  "#FF8080",
  "#FFA500",
  "#FFFF80",
  "#80FF00",
  "#80FFFF",
  "#FF80FF",
  "#FFC0C0",
  "#C0FFC0",
  "#C0E0FF",
  "#FFFFC0",
  "#C0C0FF",
  "#FFC0FF",
  "#FFD700",
  "#FFFFE0",
  "#ADFF2F",
  "#F0FFF0",
  "#F0F8FF",
  "#FFFACD",
  "#F5F5DC",
  "#FAEBD7",
  "#FFEFD5",
  "#FFF8DC",
  "#FFE4C4",
  "#FFEBCD",
  "#FFE4B5",
  "#FFDEAD",
  "#FDF5E6",
  "#FFF0F5",
  "#FAF0E6",
  "#FFE4E1",
  "#DCDCDC",
  "#F0F0F0",
  "#FFF5EE",
  "#A9A9A9",
  "#F5F5F5",
  "#FFFFF0",
];

const generateColors = (n: number) => {
  const colors = [];
  for (let i = 0; i < n; i++) {
    colors.push("#" + Math.floor(Math.random() * 16777215).toString(16));
  }
  return colors;
};

const getColors = (n: number) => {
  if (n > colors.length) {
    return [...colors, ...generateColors(n - colors.length)];
  } else {
    return colors.slice(0, n);
  }
};

export default getColors;
