import { Objects } from "@/types/Objects";
import axios from "axios";

const getObjects = () => {
  return axios
    .get(
      "https://raw.githubusercontent.com/regnumtim/objects/main/objects.json" // получаем актуальные объекты
    )
    .then((response) => {
      const oldObjects = localStorage.getItem("objects"); // получаем значение из localStorage, это может быть массив objects или undefined
      const newObjects = JSON.stringify(response.data); // получаем JSON массива objects

      if (oldObjects !== newObjects) {
        // смотрим есть ли изменения и если есть, то записываем их в локальное хранилище
        localStorage.setItem("objects", JSON.stringify(response.data, null, 0));
        window.location.reload();
      } else {
        // если нет, то используем по назначению массив objects
        return JSON.parse(newObjects) as Objects;
      }
    })
    .catch((error) => console.error(error));
};

export default getObjects;
