
import { defineComponent } from "vue";

export default defineComponent({
  name: "FieldTitleAndValue",
  emits: ["update:value"],
  props: {
    name: {
      type: String,
      required: true,
    },
    index: {
      type: Number,
      required: false,
    },
    id: {
      type: String,
      required: false,
    },
    value: {
      type: [String, Number, Array],
      required: false,
    },
    withInput: {
      required: false,
      default: false,
    },
  },
  setup() {},
});
