
import { PropType, defineComponent } from "vue";
import { Objects } from "@/types/Objects";
import { SortBy } from "@/types/SortBy";
import { LanguageData } from "@/types/LanguageData";
import { ResultsBoolean } from "@/types/ResultsBoolean";
import InputSearchByObjects from "./InputSearchByObjects.vue";
import MapObjects from "./MapObjects.vue";
import NothingWasFound from "./NothingWasFound.vue";
import CopyObjectsButton from "./CopyObjectsButton.vue";
import MainTableObjects from "./MainTableObjects.vue";
import { CurrenciesObject } from "@/types/CurrenciesObject";

export default defineComponent({
  name: "FoundObjects",
  components: {
    InputSearchByObjects,
    MapObjects,
    NothingWasFound,
    CopyObjectsButton,
    MainTableObjects,
  },
  props: {
    firstMeeting: {
      type: Boolean,
      required: true,
    },
    search: {
      type: String,
      required: true,
    },
    resultsBoolean: {
      type: Object as PropType<ResultsBoolean>,
      required: true,
    },
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    sortBy: {
      type: String as PropType<SortBy>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    currenciesObject: {
      type: Object as PropType<CurrenciesObject>,
      required: true,
    },
  },
  emits: ["update:sortBy", "update:search"],
  setup() {},
});
